import { Select } from "antd";
import { useState } from "react";
import { useMediaDevices } from "react-media-devices";
import { useZxing } from "react-zxing";
import "./App.css";

const constraints: MediaStreamConstraints = {
  video: true,
  audio: false,
};

function App() {
  const { devices } = useMediaDevices({ constraints });

  const availableCameras = devices?.filter(
    (device) => device.kind === "videoinput"
  );

  const [selectedCamera, setSelectedCamera] = useState<MediaDeviceInfo>();

  const deviceId = selectedCamera
    ? selectedCamera?.deviceId
    : availableCameras?.[0].deviceId;

  const [result, setResult] = useState("");

  const { ref } = useZxing({
    paused: !deviceId,
    deviceId,
    onDecodeResult(result) {
      setResult(result.getText());
    },
  });

  const changeCameraHandler = (value: string) => {
    const chosenCamera = availableCameras?.filter(
      (item) => item.deviceId === value
    )[0];
    setSelectedCamera(chosenCamera);
  };

  return (
    <div className="App">
      <video width={300} ref={ref} />
      <Select
        style={{ width: "90%" }}
        onChange={changeCameraHandler}
        options={availableCameras?.map((item) => ({
          label: item.label,
          value: item.deviceId,
        }))}
      />
      <p>
        <span>Last result:</span>
        <span>{result}</span>
      </p>{" "}
    </div>
  );
}

export default App;
